*,
*::after,
*::before {
  box-sizing: border-box;
}
.document {
  scroll-behavior: smooth;
  position: relative;
  height: 100%;
  padding: 0;
  margin: 0;
}
.page {
  position: relative;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Coolvetica", "Arial", sans-serif;
}
.page__background {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: url("../img/shape.svg");
  background-repeat: round;
  background-size: 60px 130px;
  background-position: center;
  z-index: -1;
}
@font-face {
  font-family: "Coolvetica";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Coolvetica.woff2") format("woff2"), url("../fonts/Coolvetica.woff") format("woff");
}
@keyframes letter {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(0.8, 1.2);
  }
  50% {
    transform: scale(1.3, 0.7);
  }
  75% {
    transform: scale(0.9, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes nav-link {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(0.8, 1.2);
  }
  50% {
    transform: scale(1.3, 0.7);
  }
  75% {
    transform: scale(0.9, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes text {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes scaling {
  0% {
    transform: scale(0.6) translate(-50%, -50%);
  }
  25% {
    transform: scale(1) translate(-50%, -50%);
  }
  50% {
    transform: scale(0.6) translate(-50%, -50%);
  }
  75% {
    transform: scale(1) translate(-50%, -50%);
  }
  100% {
    transform: scale(0.6) translate(-50%, -50%);
  }
}
@keyframes skew {
  from {
    transform: skew(90deg);
  }
  to {
    transform: skew(0);
  }
}
@keyframes error {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes rotatex {
  0% {
    transform: rotateX(0);
  }
  15% {
    transform: rotateX(95deg);
  }
  30% {
    transform: rotateX(-75deg);
  }
  45% {
    transform: rotateX(55deg);
  }
  60% {
    transform: rotateX(-35deg);
  }
  80% {
    transform: rotateX(15deg);
  }
  100% {
    transform: rotateX(0);
  }
}
@keyframes porttitle {
  from {
    opacity: 0;
    transform: perspective(500px) translate3d(-35px, -40px, -150px) rotate3d(1, -1, 0, 35deg);
  }
  to {
    opacity: 1;
    transform: perspective(500px) translate3d(0, 0, 0);
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.description {
  font-size: 18px;
  letter-spacing: 0.1em;
  text-align: justify;
  hyphens: auto;
  transform: translateX(-450px);
  transition: 0.8s;
  opacity: 0;
}
.title {
  font-size: 50px;
  margin: 0;
  margin-bottom: 40px;
  text-transform: capitalize;
  text-decoration: none;
  color: inherit;
  transition: 0.8s;
  opacity: 0;
}
.title--animated {
  animation: rotatex 2s ease-in-out;
}
.section {
  position: relative;
  min-height: 25vh;
  padding: 10vh 10vw 4vh;
  color: #000c24;
  overflow: hidden;
}
@media (max-height: 600px) {
  .section {
    height: max-content;
  }
}
@media (max-width: 767px) {
  .section {
    grid-template-columns: 1fr;
    padding: 40px 20px;
  }
}
@media (min-width: 768px) and (max-width: 1100px) {
  .section {
    padding: 2vh 60px;
  }
}
.header {
  max-width: 1240px;
  width: 100%;
  max-height: 100vh;
  margin: auto;
  transition: 1s;
}
.header__blop-wrapper {
  position: absolute;
  min-height: 100vh;
  width: 100%;
}
@media (max-height: 600px) {
  .header__blop-wrapper {
    min-height: 100%;
  }
}
@media (max-width: 767px) {
  .header__blop-wrapper {
    overflow: hidden;
  }
}
.header__blop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-height: fit-content;
  transition: all 1s;
  transition-timing-function: cubic-bezier(0.4, -0.2, 0.2, 1);
}
@media (min-width: 1100px) {
  .header__blop {
    transform: translate(-270px, -300px) scale(1) rotate(15deg);
    width: 1200px;
  }
}
@media (min-width: 768px) and (max-width: 1100px) {
  .header__blop {
    transform: translate(-240px, -200px) scale(1) rotate(15deg);
    width: 1000px;
  }
}
@media (max-width: 767px) {
  .header__blop {
    transform: translate(-250px, -240px) scale(1) rotate(15deg);
    width: 900px;
  }
}
.header__blop--preloader {
  position: fixed;
  top: 100%;
  left: 100%;
  transform: translate(-50%, -50%) scale(0.1) rotate(15deg);
  width: 100px;
}
@media (max-height: 600px) {
  .header {
    height: max-content;
  }
}
.header__logo {
  color: #ffffff;
  transition: 0.3s;
}
@media (min-width: 550px) {
  .header__logo {
    color: #000c24;
  }
}
@media (max-width: 767px) {
  .header__logo--minimazed {
    width: 30px;
    height: 30px;
  }
}
@media (max-width: 767px) {
  .header__icon--minimazed {
    transform: scale(0.5) translate(-25px, -25px);
  }
}
.header__logo:hover {
  animation-name: letter;
  animation-duration: 0.6s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
}
.header__logo:active {
  opacity: 0.3;
}
.header__logo:focus-visible {
  outline: none;
  color: #8ab1ff;
}
.letter {
  display: inline-block;
  transition: 0.3s;
}
.letter:hover {
  color: #003fbd;
}
.navigation {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 100px;
  transition: 0.8s;
}
@media (max-width: 767px) {
  .navigation {
    padding: 20px 40px;
  }
}
@media (max-width: 767px) {
  .navigation--minimazed {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 10px 20px;
    z-index: 1000;
    background-color: #000c24;
  }
}
.navigation__toggler {
  display: none;
  width: 40px;
  height: 36px;
  z-index: 2;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .navigation__toggler {
    display: block;
  }
}
.navigation__toggler-line {
  position: relative;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  transition: 0.3s;
}
.navigation__toggler-line::before {
  position: absolute;
  top: 8px;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  content: "";
  transition: 0.3s;
}
.navigation__toggler-line::after {
  position: absolute;
  top: -8px;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  content: "";
  transition: 0.3s;
}
.navigation__toggler--active .navigation__toggler-line {
  transform: translate3d(-100px, 0, 0);
}
.navigation__toggler--active .navigation__toggler-line::before {
  transform: translate3d(100px, -8px, 0) rotate(45deg);
}
.navigation__toggler--active .navigation__toggler-line::after {
  transform: translate3d(100px, 8px, 0) rotate(-45deg);
}
.navigation__list {
  display: flex;
  padding: 0;
  margin: 0;
  list-style-type: none;
  transition: 0.3s;
}
@media (max-width: 767px) {
  .navigation__list {
    position: absolute;
    top: 0;
    left: -100vw;
    height: 100vh;
    width: 50vw;
    padding-top: 100px;
    padding-left: 60px;
    flex-direction: column;
    background-color: #000c24;
    z-index: 1;
  }
}
.navigation__list--active {
  top: 0;
  left: 0;
}
.navigation__item:not(:last-of-type) {
  margin-right: 50px;
}
@media (max-width: 767px) {
  .navigation__item:not(:last-of-type) {
    margin-right: 0;
    margin-bottom: 50px;
  }
}
.navigation__link {
  color: #ffffff;
  text-decoration: none;
  opacity: 0;
  transition: 0.3s;
}
@media (max-width: 767px) {
  .navigation__link {
    color: #ffffff;
  }
}
.navigation__link:hover {
  opacity: 1;
  text-shadow: 0 0 20px #ffffff;
}
.navigation__link:focus-visible {
  text-shadow: 0 0 10px #ffffff;
  opacity: 1;
  outline: none;
}
.navigation__link--loaded {
  opacity: 0.6;
}
.navigation__link--active {
  text-shadow: 0 0 10px #000c24;
  z-index: 1;
  opacity: 1;
}
@media (max-width: 767px) {
  .navigation__link--active {
    color: #ffffff;
    text-shadow: 0 0 10px #ffffff;
  }
}
.navigation__toggler:hover .navigation__toggler-line {
  box-shadow: 0 0 10px #ffffff;
}
.navigation__toggler:hover .navigation__toggler-line::before {
  box-shadow: 0 0 10px #ffffff;
}
.navigation__toggler:hover .navigation__toggler-line::after {
  box-shadow: 0 0 10px #ffffff;
}
.hero {
  padding: 40px 100px;
}
@media (max-width: 767px) {
  .hero {
    padding-top: 20px;
    padding-left: 40px;
    padding-bottom: 80px;
    padding-right: 0;
  }
}
.hero__title {
  display: inline-block;
  margin: 0;
  color: #ffffff;
  font-size: 60px;
  letter-spacing: -5px;
  cursor: default;
}
@media (max-width: 767px) {
  .hero__title {
    font-size: 40px;
  }
}
.hero__letter {
  position: relative;
  transform: scale(0);
  transition: 0.3s;
  cursor: default;
}
.hero__description {
  margin: 40px 0;
  color: #ffffff;
  font-size: 18px;
  transform: translateY(200px);
  transition: 0.6s;
  opacity: 0;
}
.hero__social-list {
  display: flex;
  padding: 0;
  list-style-type: none;
  transform: translateY(200px);
  transition: 0.6s;
  opacity: 0;
}
.hero__social-item:not(:last-child) {
  margin-right: 30px;
}
.hero__social-button {
  color: #ffffff;
  transition: 0.3s;
}
.hero__social-button:hover {
  color: #003fbd;
}
.hero__social-button:active {
  opacity: 0.6;
}
.hero__social-button:focus-visible {
  outline: none;
  color: #003fbd;
}
.animated {
  animation-name: letter;
  animation-duration: 0.75s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
  animation-fill-mode: forwards;
}
.main {
  position: relative;
  flex-grow: 1;
}
.main__up-btn {
  position: fixed;
  bottom: 50px;
  right: 40px;
  display: none;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  z-index: 2;
  cursor: pointer;
  transition: 0.3s;
}
.main__up-btn:hover {
  transform: scale(1.1);
}
@media (max-width: 767px) {
  .main__up-btn {
    right: 20px;
  }
}
.portfolio {
  padding: 0;
}
.portfolio__title {
  max-width: 1240px;
  margin: 40px auto;
  margin-bottom: -140px;
  padding: 10vw 40px 0;
}
@media (max-width: 767px) {
  .portfolio__title {
    padding-left: 40px;
  }
}
@media (max-height: 600px) {
  .portfolio__title {
    margin: 160px auto -80px;
  }
}
.portfolio-item {
  color: #000c24;
  overflow: hidden;
}
.portfolio-item__inner {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  max-width: 1240px;
  margin: auto;
  padding: 20vh 5vw;
  gap: 40px;
  align-items: center;
}
@media (max-width: 767px) {
  .portfolio-item__inner {
    justify-items: center;
    grid-template-columns: 1fr;
    padding: 5vh 10px;
  }
}
@media (max-width: 767px) {
  .portfolio-item {
    grid-template-columns: 1fr;
    gap: 0;
    row-gap: 40px;
  }
  .portfolio-item:first-of-type {
    padding-top: 20vh;
  }
}
@media (min-width: 768px) and (max-width: 1100px) {
  .portfolio-item {
    padding: 28vh 60px;
  }
  .portfolio-item:first-of-type {
    padding-top: 40px;
  }
}
.portfolio-item--gradient {
  color: #ffffff;
  background: linear-gradient(to bottom, #000c24 0%, #00d0e8 90%, #54ccd7 100%);
}
.portfolio-item--gradient-returned {
  background: linear-gradient(to bottom, #54ccd7 0%, #00d0e8 10%, #000c24 100%);
}
.portfolio-item__text-wrapper {
  display: flex;
  padding: 0 40px;
  flex-direction: column;
  align-items: flex-start;
  transition: 0.8s;
  opacity: 0;
  transform: translateX(-500px);
}
@media (min-width: 768px) and (max-width: 1100px) {
  .portfolio-item__text-wrapper {
    padding-right: 0;
  }
}
@media (max-width: 980px) and (min-width: 767px) {
  .portfolio-item__text-wrapper {
    padding: 0;
  }
}
.portfolio-item__title {
  font-size: 50px;
  margin: 0;
  text-transform: capitalize;
  text-decoration: none;
  color: inherit;
  transition: 0.6s;
}
@media (max-width: 767px) {
  .portfolio-item__title {
    font-size: 34px;
  }
}
.portfolio-item__title:hover {
  outline: none;
  text-shadow: 0 0 20px;
}
.portfolio-item__title:active {
  text-shadow: 0 0 5px;
}
.portfolio-item__title:focus-visible {
  outline: none;
  text-shadow: 0 0 20px;
}
.portfolio-item__description {
  font-size: 18px;
  letter-spacing: 0.1em;
  opacity: 0;
  transform: perspective(100px) rotateX(180deg) translateY(1em);
  transform-origin: top center;
  transition: 0.8s;
}
.portfolio-item__buttons-list {
  display: flex;
  padding: 0;
  list-style-type: none;
  transform: perspective(50px) rotateX(180deg) translateY(1em);
  opacity: 0;
  transform-origin: top center;
  transition: 1s;
}
.portfolio-item__button-item:not(:last-of-type) {
  margin-right: 40px;
}
.portfolio-item__button {
  display: block;
  color: inherit;
  transition: 0.3s;
}
.portfolio-item__button:hover {
  color: #003fbd;
}
.portfolio-item__button:active {
  opacity: 0.6;
}
.portfolio-item__button:focus-visible {
  outline: none;
  color: #578fff;
}
.portfolio-item__image-wrapper {
  display: grid;
  align-items: end;
  grid-template-columns: repeat(8, 55px);
  transform: translateX(1000px) skew(-45deg);
  transition: 0.8s;
  grid-row: 1;
  opacity: 0;
}
@media (min-width: 768px) {
  .portfolio-item__image-wrapper {
    align-items: end;
  }
}
@media (max-width: 767px) {
  .portfolio-item__image-wrapper {
    justify-self: center;
    grid-template-columns: repeat(12, 24px);
    grid-column: 1;
    transform: none;
  }
}
.portfolio-item__picture {
  align-self: flex-end;
}
.portfolio-item__picture--mobile {
  width: max-content;
  grid-column-start: 1;
  grid-row-end: -1;
  z-index: 1;
  transition: cubic-bezier(0, 0.7, 0.2, 1.1) 0.8s;
}
@media (max-width: 980px) {
  .portfolio-item__picture--mobile {
    grid-column-start: 2;
  }
}
@media (max-width: 767px) {
  .portfolio-item__picture--mobile {
    justify-self: center;
    grid-template-columns: repeat(12, 30px);
    transform: translateX(-3000px);
  }
}
.portfolio-item__picture--desktop {
  width: max-content;
  grid-column-start: 2;
  grid-row-end: -1;
  z-index: -1;
  transition: 0.8s;
}
@media (max-width: 980px) and (min-width: 767px) {
  .portfolio-item__picture--desktop {
    grid-column-start: 1;
  }
}
@media (max-width: 767px) {
  .portfolio-item__picture--desktop {
    transform: translateY(100px);
    opacity: 0;
  }
}
.portfolio-item__picture--tablet {
  width: max-content;
  grid-column-start: 9;
  grid-row-end: -1;
  z-index: 1;
  transition: cubic-bezier(0, 0.7, 0.2, 1.1) 0.8s;
}
@media (max-width: 980px) and (min-width: 767px) {
  .portfolio-item__picture--tablet {
    grid-column-start: 5;
  }
}
@media (max-width: 767px) {
  .portfolio-item__picture--tablet {
    transform: translateX(3000px);
  }
}
.portfolio-item__image {
  display: block;
}
@media (max-width: 767px) {
  .portfolio-item__image--mobile {
    width: 48px;
    height: auto;
  }
}
@media (max-width: 767px) {
  .portfolio-item__image--desktop {
    width: 240px;
    height: auto;
  }
}
@media (max-width: 767px) {
  .portfolio-item__image--tablet {
    width: 96px;
    height: auto;
  }
}
.portfolio-item--gradient .portfolio-item__text-wrapper {
  grid-column: 1;
  transform: translateX(800px);
  opacity: 0;
}
.portfolio-item--gradient .portfolio-item__image-wrapper {
  grid-column: 2;
  transform: translateX(-1000px) skew(45deg);
}
@media (max-width: 767px) {
  .portfolio-item--gradient .portfolio-item__image-wrapper {
    grid-column: 1;
    transform: none;
  }
}
@media (max-width: 767px) {
  .portfolio-item--gradient .portfolio-item__picture--tablet {
    transform: translateX(3000px);
  }
}
@media (max-width: 767px) {
  .portfolio-item--gradient .portfolio-item__picture--mobile {
    transform: translateX(-3000px);
  }
}
.about {
  max-width: 1240px;
  margin: auto;
}
.about__description {
  max-width: 900px;
  font-size: 20px;
  line-height: 1.4em;
}
.about__description br {
  display: block;
  margin-bottom: 2em;
  content: " ";
}
.contact {
  max-width: 1240px;
  margin: auto;
  padding-bottom: 80px;
}
.contact__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
}
@media (max-width: 767px) {
  .contact__wrapper {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}
.contact__description {
  margin: 0;
  font-size: 22px;
  line-height: 1.4em;
  text-align: justify;
}
@media (max-width: 767px) {
  .contact__description {
    font-size: 20px;
  }
}
.contact__form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}
@media (max-width: 767px) {
  .contact__form {
    grid-template-columns: 1fr;
    gap: 10px;
  }
}
.contact__item {
  position: relative;
  justify-self: stretch;
  transform: translate(1000px, 0);
  opacity: 0;
  transition: 0.8s;
}
.contact__item--subject {
  grid-column: 1/-1;
}
.contact__item--message {
  grid-column: 1/-1;
}
.contact__item--invalid::after {
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  height: 3px;
  background-color: #ff0000;
  animation: error cubic-bezier(0.93, 0.02, 0.04, 1.01) 1s forwards;
  content: "";
}
@media (max-width: 767px) {
  .contact__item {
    transform: translate(0, 200px);
  }
}
.contact__field {
  display: block;
  width: 100%;
  padding: 10px;
  margin: 0;
  background-color: #ffffff;
  border: none;
  border-radius: 0;
  outline: 2px solid #000c24;
  font-family: "Coolvetica", "Arial", sans-serif;
  font-size: 20px;
  transition: 0.4s;
}
.contact__field--message {
  resize: vertical;
}
.contact__field::placeholder {
  color: rgba(0, 12, 36, 0.5);
}
.contact__field:hover {
  background-color: #f0f5ff;
}
.contact__field:focus {
  background-color: #bdd3ff;
}
.contact__submit {
  padding: 10px;
  margin: 0;
  border: none;
  outline: 2px solid #000c24;
  border-radius: 0;
  grid-column: 1/-1;
  background-color: transparent;
  color: #000c24;
  cursor: pointer;
  font-weight: 700;
  transition: all 0.3s, transform 0.8s;
  transform: translate(1000px, 0);
  opacity: 0;
}
.contact__submit:hover {
  background-color: #000c24;
  color: #ffffff;
}
.contact__submit:focus-visible {
  background-color: #000c24;
  color: #ffffff;
}
@media (max-width: 767px) {
  .contact__submit {
    transform: translate(0, 200px);
  }
}
.skills {
  max-width: 1240px;
  margin: auto;
}
.skills__list {
  display: flex;
  padding: 0;
  margin: 0;
  flex-direction: column;
  list-style-type: none;
}
.skills__item {
  display: grid;
  margin: 20px 0;
  grid-template-columns: 100px minmax(300px, 800px);
  align-items: center;
  transform: translateX(-250px);
  transition: 0.8s;
  opacity: 0;
}
@media (max-width: 767px) {
  .skills__item {
    grid-template-columns: 80px minmax(220px, 500px);
    transform: translateX(0) translateY(200px);
  }
}
.skills__name {
  margin: 0;
  font-size: 36px;
  text-transform: uppercase;
  color: rgba(0, 12, 36, 0.75);
}
@media (max-width: 767px) {
  .skills__name {
    font-size: 24px;
  }
}
.skills__range {
  position: relative;
  display: block;
  height: 60px;
  border: 5px solid #000c24;
  border-radius: 30px;
  box-shadow: 0 0 20px 4px #002e8a;
}
@media (max-width: 767px) {
  .skills__range {
    height: 40px;
    border: 2px solid #000c24;
  }
}
.skills__value {
  max-width: calc(100% - 4px);
  position: absolute;
  left: 2px;
  top: 2px;
  height: 46px;
  border-radius: 30px;
  background: linear-gradient(to right, #54ccd7 0%, #00d0e8 10%, #000c24 100%);
  transition: all 1.6s, opacity 0.4s;
  opacity: 0;
}
@media (max-width: 767px) {
  .skills__value {
    height: 32px;
  }
}
.skills__value--html {
  width: 0;
}
.skills__value--css {
  width: 0;
}
.skills__value--js {
  width: 0;
}
.html-academy {
  color: inherit;
  transition: 0.6s;
}
.html-academy:hover {
  opacity: 0.6;
}
.html-academy:active {
  opacity: 0.3;
}
.html-academy:focus-visible {
  text-shadow: 0 0 20px;
  outline: none;
}
.footer {
  padding: 8px 40px;
  background-color: #ffffff;
  z-index: 1;
}
.footer__social {
  display: grid;
  padding: 0;
  margin: 0;
  grid-template-columns: repeat(4, 40px);
  list-style-type: none;
  justify-items: center;
}
.footer__social-button {
  display: flex;
  color: #000c24;
  transition: 0.3s;
}
.footer__social-button:hover {
  color: #003fbd;
}
.footer__social-button:active {
  color: #578fff;
}
.footer__social-button:focus-visible {
  outline: none;
  color: #003fbd;
}
.modal {
  position: fixed;
  bottom: 60px;
  left: 0;
  padding: 20px 10px;
  text-align: center;
  transform: translateX(-1000px);
  transition: cubic-bezier(0, 0.7, 0.2, 1.1) 0.5s;
}
@media (max-width: 767px) {
  .modal {
    bottom: 50px;
  }
}
.modal--show {
  transform: translateX(0);
}
.modal--success {
  background-color: #00d50e;
}
.modal--error {
  background-color: #ff0000;
}
.modal__description {
  font-size: 16px;
  margin: 0;
}
